// store.js
import { createStore } from 'vuex';
import axios from 'axios';

export default createStore({
  state: {
    info: ''
  },
  mutations: {
    setInfo(state, info) {
      state.info = info;
    }
  },
  actions: {
    async fetchInfo({ commit }) {
      try {
        const websiteId = '5ca5d26765f44b5702766c0234c585e6';
        const url = 'http://weka-ai-website-api.ai-site.cc/api/get-website-data?websiteId=' + websiteId;
        const response = await axios.get(url);
        const responseData = response.data;
        const websiteData = JSON.parse(responseData.resp_data);
        websiteData.routes = [
          { path: '/about', name: '关于我们', key: 'introduction' },
          { path: '/culture', name: '企业文化', key: 'comculture' }
        ];
        //console.log(websiteData);
        commit('setInfo', websiteData);
      } catch (error) {
        console.error(error);
      }
    }
  }
});